.menu-btn {
    position: absolute;
    z-index: 3;
    right: 35px;
    top: 35px;
    cursor: pointer;
    @include easeOut;

    .btn-line {
        width: 28px;
        height: 3px;
        margin: 0 0 5px 0;
        background: set-text-color($primary-color);
        @include easeOut;
    }
    .btn-line-last {
        width: 20px;
        height: 3px;
        margin: 0 0 5px 0;
        background: set-text-color($primary-color);
        @include easeOut;
    }

    //Rotate to X with Menu Lines
    &.close {
        transform: rotate(180deg);

        .btn-line {
            &:nth-child(1){
                transform: rotate(45deg) translate(5px, 5px);
            }
            &:nth-child(2){
                opacity: 0;
            } 
        }
        .btn-line-last {
            width: 28px;
            transform: rotate(-45deg) translate(7px, -6px);
        }
    }
}

//menu overlay

.menu {
    position: fixed;
    top: 0;
    width: 100%;
    opacity: 0.9;
    visibility: hidden;

    &.show {
        visibility: visible;
    }
    
    &-branding,
    &-nav {
        display: flex;
        flex-flow: column wrap;
        align-items: center;
        justify-content: center;
        float: left;
        width: 50%;
        height: 100vh;        
        overflow: hidden;
    }
    &-nav {
        margin: 0;
        padding: 0;
        background: darken($primary-color, 5);
        list-style: none;
        transform: translate3d(0, -100%, 0);
        @include easeOut;

        &.show {
            transform: translate3d(0,0,0);
        }
    }

    &-branding {
        background: $primary-color;
        transform: translate3d(0, 100%, 0);
        @include easeOut;
        
        &.show {
            transform: translate3d(0,0,0);
        }

        .portrait {
            width: 250px;
            height: 250px;
            background: url('../img/profile_brown.jpg') no-repeat;
            border-radius: 50%;
            border: solid 3px $secondary-color;
           
        }
    }

    .nav-item {
        transform: translate3d(600px,0,0);
        @include easeOut;

        &.show {
            transform: translate3d(0,0,0);
        }
        &.current >a {
            color: $secondary-color;
        }
    }

    .nav-link {
        display: inline-block;
        position: relative;
        font-size: 2rem;
        text-transform: uppercase;
        padding: 1rem 0;
        font-weight: 300;
        color: set-text-color($primary-color);
        text-decoration: none;
        @include easeOut;

        &:hover {
            color: $secondary-color;
        }
    }
    
}

// delay each nav item slide by 0.1s
@for $x from 1 through 4 {
    .nav-item:nth-child(#{$x}) {
        transition-delay: $x * 0.1s;
    }
}